<template>
  <div id="app">
    <nav-bar></nav-bar>
    <router-view/>
    <my-footer></my-footer>
  </div>
</template>

<script>
import NavBar from "./components/NavBar.vue"
import myFooter from "./components/Footer.vue"

export default {
  name: 'App',
  components: {
    NavBar,
    myFooter
  }
}
</script>

<style lang="scss">
@import '../node_modules/bootstrap/scss/bootstrap.scss';  
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #465e65;
}

::-webkit-scrollbar {  /*滾動條寬度*/
  width: 10px; 
}
::-webkit-scrollbar-button {  /*滾動按鈕*/
  background: #c99e8c;
}
::-webkit-scrollbar-track-piece {  /*滾動條軌道*/
  background: #c99e8c7f;
}
::-webkit-scrollbar-thumb {  /*滾動條樣式*/
  border-radius: 4px;
  background-color: #465e65;
}
::-webkit-scrollbar-track {  /*滾動軌道樣式*/
  box-shadow: transparent;
}

h1, h2, h3, h4, h5, h6,
.h1, .h2, .h3, .h4, .h5, .h6{
	font-weight: bold;
}

.container{
  padding: 5rem 30px 8rem 30px;
  background-color: white;
}
</style>


