<template>
<nav class="navbar navbar-expand-lg navbar-dark">
    <a class="navbar-brand" href="/"></a>
    <button class="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarSupportedContent"
        aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
        <span class="navbar-toggler-icon"></span>
    </button>

    <div class="collapse navbar-collapse" id="navbarSupportedContent">
        <div class="navbar-nav mr-auto">
          <li class="nav-item">
            <router-link to="/" class="nav-link">{{ $t("Dessert_Calculator") }}</router-link>
          </li>
          <li class="nav-item">
            <router-link to="/converter" class="nav-link">模具尺寸換算</router-link>
          </li>
          <li class="nav-item">
            <router-link to="/tips" class="nav-link">烘焙小tips</router-link>
          </li>
          <li class="nav-item">
            <router-link to="/feedback" class="nav-link">{{ $t("Feedback") }}</router-link>
          </li>
        </div>
        
        <select v-model="currentLang" @change="setLang($event.target.value)">
          <option v-for="option in optionsLang" :value="option.value" class="option">{{ option.text }}</option>
        </select>
    </div>
</nav>
</template>

<script>
export default {
  name: 'NavBar',
  data(){
    return{
      optionsLang:[
        {text: "繁體中文", value: "zh-tw"},
        {text: "English", value: "en"},
      ],
      currentLang: ""
    }

  },

  methods: {
    setLang(value){
      this.$store.commit("setLang", value);
      this.$i18n.locale = value;
      localStorage.setItem("lang", value);
      this.currentLang = value;
      // console.log("換語言to", value)
    }
  },

  mounted() {
    if(localStorage.getItem("lang") != undefined){
      this.setLang(localStorage.getItem("lang"))
    }
  }
}
</script>

<style scoped>
.navbar{
  background-color: #465e65;
}
.navbar-dark .navbar-nav .nav-link{
  color: #c99e8ca5;
}
.navbar-dark .navbar-nav .nav-link:hover{
  color: #c99e8c;
}
.router-link-exact-active{
  color: #c99e8c  !important;
  font-weight: bold;
}

select{
  padding: 0.2rem 2rem 0.2rem 0.5rem;
  color: #465e65;
  border-radius: 0.3rem;

  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  background: url("../assets/icon/down_arrow.png") 90% / 15% no-repeat #c99e8c;
}
select:focus{
  outline: none;
}
select option{
  background-color: #c99e8cb4;
}
</style>



