<template>
	<div class="container">

		<h1>{{ $t("Dessert_Calculator") }}</h1>
		<div>
			<div v-for="(list, key, index) in data" class="class m-3 p-4">
				<h2>{{ $t(key) }}</h2>
				<div class="row justify-content-center">
					<div v-for="item in list" class="mx-4 my-3 text-left">
						<h4>{{ $t(item.name) }}
							<span v-show="item.name=='雞蛋'" class="my-note">1≒54.5{{ $t("__g") }}</span>
							<span v-show="item.name=='蛋白'" class="my-note">1≒39{{ $t("__g") }}</span>
							<span v-show="item.name=='蛋黃'" class="my-note">1≒17{{ $t("__g") }}</span>
						</h4>
						
						<span class="text-nowrap">
							<input type="text" v-model="item.weight" ref="weight"> {{ $t("__g") }}
						</span>

						<p v-show="item.show == false" @click="item.show=true" class="nutrients_click">{{$t("__click_to_see")}} {{$t("__nutrition_facts")}}</p>
						<p v-show="item.show == true" @click="item.show=false" class="nutrients_click">{{$t("__close")}} {{$t("__nutrition_facts")}}</p>
						<div v-show="item.show == true" class="nutrients rounded p-3">
							<p>{{ $t("__calories") }} <span class="n-calories">{{ (item.calories * item.weight).toFixed(2) }}</span> {{ $t("__kcal") }}</p>
							<p>{{ $t("__protein") }} <span class="n-protein">{{ (item.protein * item.weight).toFixed(2) }}</span> {{ $t("__g") }}</p>
							<p>{{ $t("__fat") }} <span class="n-fat">{{ (item.fat * item.weight).toFixed(2) }}</span> {{ $t("__g") }}</p>
							<p>{{ $t("__satFat") }} <span class="n-satFat">{{ (item.satFat * item.weight).toFixed(2) }}</span> {{ $t("__g") }}</p>
							<p>{{ $t("__carb") }} <span class="n-carb">{{ (item.carb * item.weight).toFixed(2) }}</span> {{ $t("__g") }}</p>
							<p>{{ $t("__dietaryFiber") }} <span class="n-dietaryFiber">{{ (item.dietaryFiber * item.weight).toFixed(2) }}</span> {{ $t("__g") }}</p>
							<p>{{ $t("__Na") }} <span class="n-Na">{{ (item.Na * item.weight).toFixed(2) }}</span> {{ $t("__mg") }}</p>
							<p>{{ $t("__K") }} <span class="n-K">{{ (item.K * item.weight).toFixed(2) }}</span> {{ $t("__mg") }}</p>
							<p>{{ $t("__Ca") }} <span class="n-Ca">{{ (item.Ca * item.weight).toFixed(2) }}</span> {{ $t("__mg") }}</p>
							<p>{{ $t("__Mg") }} <span class="n-Mg">{{ (item.Mg * item.weight).toFixed(2) }}</span> {{ $t("__mg") }}</p>
							<p>{{ $t("__Fe") }} <span class="n-Fe">{{ (item.Fe * item.weight).toFixed(2) }}</span> {{ $t("__mg") }}</p>
							<p>{{ $t("__Zn") }} <span class="n-Zn">{{ (item.Zn * item.weight).toFixed(2) }}</span> {{ $t("__mg") }}</p>
						</div>
					</div>
				</div>
			</div>
			<hr class="hr-lg">
		</div>

		<button @click="get_total" class="total-btn">{{$t("__calculate")}}</button>
		<div class="row justify-content-center">
			<p class="col-md-4 col-sm-11 row justify-content-center text-center">{{ $t("__total_weight") }} 
				<span class="align-bottom"><span style="font-size: 3rem; font-weight: bold;">{{ total.weight }}</span> {{ $t("__g") }}</span>
			</p>
			<p class="col-md-4 col-sm-11 row justify-content-center text-center">{{ $t("__total_calories") }} 
				<span class="align-bottom"><span style="font-size: 3rem; font-weight: bold;">{{ total.calories }}</span> {{ $t("__kcal") }}</span>
			</p>
		</div>
		<table class="table table-hover">
			<tr>
				<th style="width: 20%; min-width: 6rem;"></th>
				<th style="width: 34%;">{{ $t("__amount" )}}</th>
				<th style="width: 34%;">共<input type="text" v-model="single[0]" @input="get_single(0)" class="table-input1">份，<br>{{ $t("__serving_size") }}</th>
				<th style="width: 12%; min-width: 3rem;">{{ $t("__unit") }}</th>
			</tr>
			<tr>
				<th>{{ $t("__weight") }}</th>
				<td>{{ total.weight }}</td>
				<td><input type="text" v-model="single[1]" @input="get_single(1)" class="table-input2"></td>
				<td>{{ $t("__g") }}</td> 
			</tr>
			<tr>
				<th>{{ $t("__calories") }}</th>
				<td>{{ total.calories }}</td>
				<td>{{ (total.calories/single[0]).toFixed(2) || "" }}</td>
				<td>{{ $t("__kcal") }}</td>
			</tr>
			<tr>
				<th>{{ $t("__protein") }}</th>
				<td>{{ total.protein }}</td>
				<td>{{ (total.protein/single[0]).toFixed(2) || "" }}</td>
				<td>{{ $t("__g") }}</td>
			</tr>
			<tr>
				<th>{{ $t("__fat") }}</th>
				<td>{{ total.fat }}</td>
				<td>{{ (total.fat/single[0]).toFixed(2) || "" }}</td>
				<td>{{ $t("__g") }}</td>
			</tr>
			<tr>
				<th>{{ $t("__satFat") }}</th>
				<td>{{ total.satFat }}</td>
				<td>{{ (total.satFat/single[0]).toFixed(2) || "" }}</td>
				<td>{{ $t("__g") }}</td>
			</tr>
			<tr>
				<th>{{ $t("__carb") }}</th>
				<td>{{ total.carb }}</td>
				<td>{{ (total.carb/single[0]).toFixed(2) || "" }}</td>
				<td>{{ $t("__g") }}</td>
			</tr>
			<tr>
				<th>{{ $t("__dietaryFiber") }}</th>
				<td>{{ total.dietaryFiber }}</td>
				<td>{{ (total.dietaryFiber/single[0]).toFixed(2) || "" }}</td>
				<td>{{ $t("__g") }}</td>
			</tr>
			<tr>
				<th>{{ $t("__Na") }}</th>
				<td>{{ total.Na }}</td>
				<td>{{ (total.Na/single[0]).toFixed(2) || "" }}</td>
				<td>{{ $t("__mg") }}</td>
			</tr>
			<tr>
				<th>{{ $t("__K") }}</th>
				<td>{{ total.K }}</td>
				<td>{{ (total.K/single[0]).toFixed(2) || "" }}</td>
				<td>{{ $t("__mg") }}</td>
			</tr>
			<tr>
				<th>{{ $t("__Ca") }}</th>
				<td>{{ total.Ca }}</td>
				<td>{{ (total.Ca/single[0]).toFixed(2) || "" }}</td>
				<td>{{ $t("__mg") }}</td>
			</tr>
			<tr>
				<th>{{ $t("__Mg") }}</th>
				<td>{{ total.Mg }}</td>
				<td>{{ (total.Mg/single[0]).toFixed(2) || "" }}</td>
				<td>{{ $t("__mg") }}</td>
			</tr>
			<tr>
				<th>{{ $t("__Fe") }}</th>
				<td>{{ total.Fe }}</td>
				<td>{{ (total.Fe/single[0]).toFixed(2) || "" }}</td>
				<td>{{ $t("__mg") }}</td>
			</tr>
			<tr>
				<th>{{ $t("__Zn") }}</th>
				<td>{{ total.Zn }}</td>
				<td>{{ (total.Zn/single[0]).toFixed(2) || "" }}</td>
				<td>{{ $t("__mg") }}</td>
			</tr>
		</table>
	</div>
</template>

<script>
import data from '../../static/foods_db.json'

export default {
	name: 'calculator',
	data(){
		return {
			data: data,
			foods_class: ["__oil", "__egg", "__sugar", "__flour"],
			total: {
				"weight": 0,
				"calories": 0,
				"protein": 0,
				"fat": 0,
				"satFat": 0,
				"carb": 0,
				"dietaryFiber": 0,
				"Na": 0,
				"K": 0,
				"Ca": 0,
				"Mg": 0,
				"Fe": 0,
				"Zn": 0
			},
			single: [0, 0]
		}
	},

	methods:{
		get_total(){
			this.total = {
				"weight": 0, "calories": 0, "protein": 0, "fat": 0, "satFat": 0, "carb": 0, "dietaryFiber": 0, "Na": 0, "K": 0, "Ca": 0, "Mg": 0, "Fe": 0, "Zn": 0
			};
			let keys = Object.keys(this.total)
			let count = 0;
			for(let i in this.data){
				for(let j in this.data[i]){
					count++;
				}
			}

			for(let c=0; c<count; c++){
				this.total.weight += Number(this.$refs.weight[c].value);
				keys.slice(1).forEach((key) => {
					this.total[key] += Number(document.getElementsByClassName("n-"+key)[c].innerHTML);
				})
			}
			keys.forEach((key) => {
				this.total[key] = this.total[key].toFixed(2);
			})
		},
		get_single(i){
			if(i==0){ this.single[1] = (this.total.weight/this.single[0]).toFixed(2) }
			else if(i==1){ this.single[0] = (this.total.weight/this.single[1]).toFixed(2) }
		}
	},
	mounted(){
		this.$nextTick(() => {
		})
	},
}
</script>

<style scoped>
input{
	padding: 0.3rem 0.5rem;
	color: #465e65;
	background-color: #f0f0f0;
	border: 0;
	border-radius: 0.2rem;
}
input:focus{
	outline: 2px #465e65 solid;
}

.my-note{
	font-size: 0.9rem;
	font-weight: 400;
}
.nutrients{
	background-color: #c99e8c66;
	/* border-radius: 1rem; */
}
.nutrients p{
	margin: 0;
}
.nutrients_click{
	margin: 0;
	color: #c99e8cc5;
	cursor: pointer;
}

.hr-sm{
	width: 100%;
	border-top: 1px #465e65 solid;
	border-radius: 2rem; 
}
.hr-lg{
	width: 100%;
	border-top: 4px #465e65 solid;
	border-radius: 2rem; 
}

.total-btn{
	margin: 2rem 0;
	padding: 0.5rem 1rem;
	font-size: 1.3rem;
	font-weight: 700;
	color: #c99e8c;
	background-color: #465e65;
	border: 0;
	border-radius: 0.3rem;
}
.table th, .table td {
    padding: 0.5rem;
    vertical-align: middle;
}
.table th{
    padding: 0.5rem;
	color: #c99e8c;
	background-color: #465e65;
    vertical-align: middle;
}
.table td{
    padding: 0.5rem;
	color: #465e65;
	background-color: #c99e8c;
    vertical-align: middle;
}
.table-input1{
	width: 4rem;
	margin: 0 0.3rem;
	color: #465e65;
	background-color: #c99e8ca4;
	text-align: center;
}
.table-input2{
	width: 5rem;
	color: #c99e8c;
	background-color: #465e65a4;
	text-align: center;
}
</style>



